import { SimpleChanges, Renderer2, forwardRef, Injector, HostBinding, ChangeDetectorRef } from '@angular/core';
import { NgZone, ViewEncapsulation, TemplateRef } from '@angular/core';
import { Component, OnInit, Output, EventEmitter, ElementRef, Input, AfterViewInit, ViewChild, OnChanges } from '@angular/core';
import { NG_VALUE_ACCESSOR, ControlValueAccessor, NgControl, MaxLengthValidator } from '@angular/forms';
import ResizeObserver from 'resize-observer-polyfill';
export const INPUT_GROUP_VALUE_ACCESSOR: any = {
    provide: NG_VALUE_ACCESSOR,
    useExisting: forwardRef(() => InputGroupComponent),
    multi: true
};

@Component({
    selector: 'farrisui-input-group, input-group, farris-input-group',
    template: `
        <div #inputGroup [class]="customCls + ' input-group'"
            [class.f-state-disabled]="disabled"
            [class.f-state-editable]="editable && !disabled && !readonly"
            [class.f-state-readonly]="readonly && !disabled" [style.border-width.px]="noborder ? 0: 1">
            <span class="input-group-before-tips" *ngIf="useExtendInfo" (mouseenter)="onMouseOverInExtentInfo()"
                farrisPopover [popover]="extendInfo" [triggers]="'hover'" [container]="'body'" [placement]="'top'">
                <i class="f-icon f-icon-info-circle"></i><b class="tips-arrow"></b>
            </span>

            <input
                #textbox
                name="input-group-value"
                class="form-control f-utils-fill"
                [class.text-left]="textAlign == 'left'"
                [class.text-center]="textAlign == 'center'"
                [class.text-right]="textAlign == 'right'"
                [attr.title]="enableTitle? value: ''"
                [attr.type]="inputType"
                [attr.placeholder]="(disabled||readonly)&&!forcePlaceholder?'':placeholder"
                [attr.autocomplete]="autocomplete"
                [readonly]="readonly || !editable"
                [ngModel]="value"
                [disabled]="disabled"
                (blur)="onBlur($event)"
                (mousedown)="onMousedown($event)"
                (ngModelChange)="onValueChange($event)"
                (keydown.enter)="onEnter($event)"
                (focus)="onInputFocus($event)"
                (click)="onInputClick($event)"
                minlength="{{minLength}}"
                maxlength="{{maxLength}}"
                (keyup)="keyupHandle.emit($event)"
                (keydown)="keydownHandle.emit($event)"
                [attr.tabindex]="tabIndex"
            />
            <div class="input-group-append" [ngClass]="{'append-force-show':showButtonWhenDisabled && (readonly||disabled)}">
                <span *ngIf="enableClear && !readonly && !disabled"
                    [style.display]="showClearButton ? 'flex' : 'none'"
                    class="input-group-text input-group-clear" #clearIcon
                    (click)="onClearValue($event)" style="width:24px">
                    <i class="f-icon modal_close"></i>
                </span>
                <span class="input-group-text"
                    (mouseenter)="onIconMouseEnter($event)" (mouseleave)="onIconMouseLeave($event)"
                    *ngIf="groupText && !groupTextTemplate" [innerHTML]="groupText | safe: 'html'"
                    (click)="onClickHandle($event)"></span>
                <ng-container *ngIf="groupTextTemplate">
                    <ng-container *ngTemplateOutlet="groupTextTemplate" ></ng-container>
                </ng-container>
            </div>
        </div>
    `,
    styles: [
        `
            .input-group {
                flex-wrap: nowrap;
            }
            .input-group-text {
                cursor: pointer;
            }
            .input-group-clear {
                cursor: pointer;
            }

            .input-group-text:hover {
                background: #ccc;
            }

            .input-group-clear-right {
                position: absolute;
                height: 100%;
                right: 0;
                top: 0;
                z-index: 100;
            }
        `
    ],
    // .input-group-clear:hover {
    //     background: #e9ecef !important;
    // }
    providers: [INPUT_GROUP_VALUE_ACCESSOR],
    encapsulation: ViewEncapsulation.None
})
export class InputGroupComponent
    implements OnInit, AfterViewInit, OnChanges, ControlValueAccessor {
    @HostBinding('class.f-cmp-inputgroup')
    @Input() autocomplete = 'off';
    showClearButton = false;
    ngControl: NgControl;
    @Input() value = '';
    /** åªè¯» */
    @Input() readonly = false;
    /** ç¦ç¨ */
    @Input() disabled = false;
    /** åè®¸ç¼è¾ */
    @Input() editable = true;
    /** å¯ç¨æ¸é¤æé® */
    @Input() enableClear = true;
    /** æ©å±æé® */
    @Input() groupText = '';
    /** èªå®ä¹CLASS */
    @Input() customCls = '';
    /** å½ç»ä»¶ç¦ç¨æåªè¯»æ¶æ¾ç¤ºåè¾¹çæé® */
    @Input() showButtonWhenDisabled = false;
    /** å¯ç¨æç¤ºä¿¡æ¯ */
    @Input() placeholder = '';
    /** ææ¬å¨è¾å¥æ¡ä¸­çå¯¹é½æ¹å¼ */
    @Input() textAlign = 'left';

    @Input() groupTextTemplate: TemplateRef<any>;
    @Input() isPassword = false;
    @Input() enableViewPassword = true;

    @Input() noborder = false;

    @Input() minLength: number | undefined = undefined;
    @Input() maxLength: number | undefined = undefined;

    @Input() tabIndex: number;

    @Input() enableTitle = true;
    /** æ©å±ä¿¡æ¯ï¼å¨è¾å¥æ¡åé¢ æ¾ç¤º â  å¾æ é¼ æ æ»è¿åæ¾ç¤º */
    @Input() useExtendInfo = false;
    @Input() extendInfo = '';
    @Input() forcePlaceholder = false;
    @Output() updateExtendInfo = new EventEmitter();

    @Output() clear = new EventEmitter();
    @Output() valueChange = new EventEmitter();
    @Output() clickHandle = new EventEmitter();
    @Output() blurHandle = new EventEmitter();
    @Output() focusHandle = new EventEmitter();
    @Output() enterHandle = new EventEmitter();
    @Output() iconMouseEnter = new EventEmitter();
    @Output() iconMouseLeave = new EventEmitter();
    @Output() keyupHandle = new EventEmitter();
    @Output() keydownHandle = new EventEmitter();
    @Output() inputClick = new EventEmitter();

    @ViewChild('inputGroup') inputGroup: ElementRef;
    @ViewChild('textbox') textbox: ElementRef;
    @ViewChild('clearIcon') clearIconRef: ElementRef;

    inputType = 'text';

    private state: boolean;
    cd: ChangeDetectorRef = null;

    private focusCls = 'f-state-focus';
    private ro: ResizeObserver = null;
    onModelChange = (obj: any) => { };
    onModelTouched = (val: any) => { };


    private openEyeICON = '<span class="f-icon f-icon-eye" style="color: rgb(56, 143, 255);"></span>';
    private closeEyeICON = '<span class="f-icon f-icon-eye"></span>';

    constructor(
        public el: ElementRef,
        private renderer: Renderer2,
        private ngZone: NgZone,
        private injector: Injector
    ) {
        this.cd = this.injector.get(ChangeDetectorRef);
    }

    ngOnInit() {
        if (!this.groupText) {
            this.renderer.setStyle(
                this.el.nativeElement.querySelector('.input-group-append'),
                'margin-left',
                '0'
            );
        }

        this.initSmartPassword();


        // if (!this.editable) {
        //     this.readonly = true
        // }
    }

    ngAfterViewInit() {
        this.ngZone.runOutsideAngular(() => {
            if (this.enableClear) {
                this.inputGroup.nativeElement.addEventListener(
                    'mouseenter',
                    this.onMouseEnter.bind(this)
                );
                this.inputGroup.nativeElement.addEventListener(
                    'mouseleave',
                    this.onMouseLeave.bind(this)
                );
            }

            this.ro = new ResizeObserver((entries, observer) => {
                if (entries && entries[0] && this.clearIconRef) {
                    const { width, height } = entries[0].contentRect;
                    const target =  entries[0].target;

                    if (width < 70) {
                        this.clearIconRef.nativeElement.classList.add('input-group-clear-right');
                    } else {
                        this.clearIconRef.nativeElement.classList.remove('input-group-clear-right');
                    }
                }
            });
            this.ro.observe(this.inputGroup.nativeElement);

        });

    }

    ngOnChanges(changes: SimpleChanges) {
        if (changes.groupText && !changes.groupText.isFirstChange()) {
            if (changes.groupText.currentValue) {
                this.renderer.setStyle(
                    this.el.nativeElement.querySelector('.input-group-append'),
                    'margin-left',
                    '-1'
                );
            }
        }

        ['readonly', 'disabled'].forEach(n => {
            if (changes[n] && !changes[n].isFirstChange()) {
                if (this[n] && this.isPassword) {
                    if (this.groupText) {
                        this.groupText = this.closeEyeICON;
                    }
                    this.inputType = 'password';
                }
            }
        })
    }

    ngModelChange(val: any) {
    }


    private initSmartPassword() {
        if (this.isPassword) {
            
            this.groupText = this.closeEyeICON;

            if (!this.enableViewPassword) {
                this.groupText = '';
            }

            this.inputType = 'password';
            let openEyes = false;
            this.onClickHandle = ($event) => {
                openEyes = !openEyes;
                const inputtype = openEyes ? 'text' : 'password';
                this.groupText = openEyes ? this.openEyeICON : this.closeEyeICON;
                this.inputType = inputtype;
                this.cd.detectChanges();
                return false;
            };
        } else {
            this.inputType = 'text';
        }
    }

    onEnter($event: KeyboardEvent) {
        // if (this.editable) {
        //     $event.stopPropagation();
        // }
        this.enterHandle.emit({originalEvent: $event});
    }

    onInputFocus($event) {
        if (this.disabled) {
            return;
        } else {
            if (!this.noborder) {
                this.renderer.addClass(this.inputGroup.nativeElement, this.focusCls);
            }
            if (this.editable || !this.readonly) {
                this.focusHandle.emit($event);
            }
        }
    }

    onInputClick($event) {
        this.inputClick.emit($event);
    }


    onMouseEnter(event) {
        if (this.value) {
            if (!this.editable) {
                if (!this.state && !this.disabled) {
                    this.showClearButton = true;
                }
            } else {
                if (!this.readonly && !this.disabled) {
                    this.showClearButton = true;
                }
            }
        }
        if (this.showClearButton) {
            this.toggleClearIcon(event, true);
        }
    }

    onMouseLeave(event) {
        this.showClearButton = false;
        this.toggleClearIcon(event, false);
    }

    getStateCls() {
        if (this.disabled) {
            return { 'f-state-disabled': true };
        } else {
            if (!this.editable) {
                return { 'f-state-editable': true, 'f-state-readonly': this.readonly };
            } else {
                return { 'f-state-readonly': this.readonly };
            }
        }
    }

    private toggleClearIcon(event: any, isShow = false) {
        const str = isShow ? '' : 'none';
        const clearIcon = event.target.querySelector('.input-group-clear');
        if (clearIcon) {
            clearIcon.style.display = str;
        }
    }

    onMouseOverInExtentInfo() {
        this.updateExtendInfo.emit();
    }

    onMousedown($event: MouseEvent) {
        const target = $event.target as HTMLElement;
        if (target.tagName !== 'INPUT') {
            $event.preventDefault();
        }
        $event.stopPropagation();
    }

    onClearValue($event: any) {
        const flag1 = !this.readonly && !this.disabled && this.editable;
        const flag2 = !this.editable;
        $event.stopPropagation();
        if (flag1 || flag2) {
            this.onValueChange('', false);
            this.showClearButton = false;
            this.clear.emit();
        }
    }

    onBlur(event) {
        this.renderer.removeClass(this.inputGroup.nativeElement, this.focusCls);
        this.blurHandle.emit(event);
        event.stopPropagation();
        return false;
    }

    onValueChange(val: string, emit = true) {
        if (this.value !== val) {
            this.value = val;
            this.onModelChange(val);
            this.onModelTouched(val);
            if (emit) {
                this.valueChange.emit(val);
            }
        }
    }

    onClickHandle(event?: Event) {
        if (this.showButtonWhenDisabled || ((!this.editable  || !this.readonly) && !this.disabled)) {
            this.clickHandle.emit({
                originalEvent: event,
                value: this.value
            });
        }

        if (event) {
            event.stopPropagation();
        }
    }
    onIconMouseEnter(e: MouseEvent) {
        this.iconMouseEnter.emit(e);
    }
    onIconMouseLeave(e: MouseEvent) {
        this.iconMouseLeave.emit(e);
    }
    focus() {
        this.textbox.nativeElement.focus();
    }

    writeValue(obj: any): void {
        this.value = (obj === null || obj === undefined) ? '' : obj;
        this.textbox.nativeElement.value = this.value;
        // å¨æ­¤å¤æ§è¡ onModelChange å pristine ç¶æåä¸º false å³éç¬¬ä¸æ¬¡å è½½ï¼å¯¼è³è¡¨åä¸­çéè¯¯ä¿¡æ¯æ¾ç¤ºåºæ¥ï¼
        // å¦æå¿é¡»è¦æ§è¡ä¸æ¬¡ï¼éè¦éæ°ngControl çç¶æ
        // this.onModelChange(obj);
    }
    registerOnChange(fn: any): void {
        this.onModelChange = fn;
    }
    registerOnTouched(fn: any): void {
        this.onModelTouched = fn;
    }
    setDisabledState(isDisabled: boolean): void {
        this.disabled = isDisabled;
    }

    setFocusToEnd() {
        const el = this.textbox.nativeElement;
        el.focus();
        el.selectionStart = el.value.length;
        el.selectionEnd = el.value.length;
    }
}
